'use client';

import { useState } from 'react';

// --- Type Definitions ---
interface HistoryEntry {
  date: string;
  sentiment: 'POSITIVE' | 'NEGATIVE' | 'NEUTRAL' | string;
  labels: string[];
  activities: string[];
}

interface SummaryData {
  totalEntries: number;
  moods: { [key: string]: number };
  topEmotion: string;
  topActivity: string;
}

// --- Main Component ---
export default function WeeklySummary() {
  const [showModal, setShowModal] = useState(false);
  const [summary, setSummary] = useState<SummaryData | null>(null);

  const calculateSummary = () => {
    const history: HistoryEntry[] = JSON.parse(localStorage.getItem('aura-history') || '[]');
    const sevenDaysAgo = new Date();
    sevenDaysAgo.setDate(sevenDaysAgo.getDate() - 7);

    // 1. Filter entries for the last 7 days
    const recentEntries = history.filter(entry => new Date(entry.date) > sevenDaysAgo);

    if (recentEntries.length === 0) {
      setSummary({ totalEntries: 0, moods: {}, topEmotion: 'N/A', topActivity: 'N/A' });
      return;
    }

    // 2. Calculate statistics
    const moodCounts: { [key: string]: number } = { POSITIVE: 0, NEGATIVE: 0, NEUTRAL: 0 };
    const labelCounts: { [key: string]: number } = {};
    const activityOnPositiveDays: { [key: string]: number } = {};

    recentEntries.forEach(entry => {
      // Count moods
      if (moodCounts[entry.sentiment] !== undefined) {
        moodCounts[entry.sentiment]++;
      }
      
      // Count labels/emotions
      entry.labels.forEach(label => {
        labelCounts[label] = (labelCounts[label] || 0) + 1;
      });

      // Count activities on positive days
      if (entry.sentiment === 'POSITIVE') {
        entry.activities.forEach(activity => {
          activityOnPositiveDays[activity] = (activityOnPositiveDays[activity] || 0) + 1;
        });
      }
    });

    // Find the most frequent label and activity
    const getTopItem = (obj: { [key: string]: number }) => Object.keys(obj).reduce((a, b) => obj[a] > obj[b] ? a : b, 'N/A');
    
    setSummary({
      totalEntries: recentEntries.length,
      moods: moodCounts,
      topEmotion: getTopItem(labelCounts),
      topActivity: getTopItem(activityOnPositiveDays),
    });
  };

  const handleShowSummary = () => {
    calculateSummary();
    setShowModal(true);
  };

  const MoodStat = ({ mood, count, total }: { mood: string, count: number, total: number }) => {
    const percentage = total > 0 ? Math.round((count / total) * 100) : 0;
    const colors: { [key: string]: string } = {
      POSITIVE: 'bg-green-500',
      NEGATIVE: 'bg-red-500',
      NEUTRAL: 'bg-gray-500',
    };
    return (
      <div>
        <div className="flex justify-between font-semibold text-sm">
          <span>{mood}</span>
          <span>{percentage}%</span>
        </div>
        <div className="w-full bg-gray-200 rounded-full h-2.5 mt-1">
          <div className={`${colors[mood]} h-2.5 rounded-full`} style={{ width: `${percentage}%` }}></div>
        </div>
      </div>
    );
  };


  return (
    <>
      <div className="my-8 text-center">
        <button
          onClick={handleShowSummary}
          className="bg-white px-6 py-3 rounded-lg shadow-md font-semibold text-gray-700 hover:bg-gray-100 transition-colors"
        >
          Generate My Weekly Summary
        </button>
      </div>

      {showModal && summary && (
        <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4 animate-fade-in">
          <div className="bg-white rounded-lg shadow-2xl p-6 w-full max-w-md">
            <div className="flex justify-between items-center border-b pb-3 mb-4">
              <h2 className="text-2xl font-bold text-gray-800">Your Week in Review</h2>
              <button onClick={() => setShowModal(false)} className="text-2xl font-bold text-gray-500 hover:text-gray-800">&times;</button>
            </div>
            
            {summary.totalEntries > 0 ? (
              <div className="space-y-5">
                <div>
                  <h3 className="font-bold text-lg mb-2">Mood Breakdown</h3>
                  <div className="space-y-3">
                    <MoodStat mood="POSITIVE" count={summary.moods.POSITIVE} total={summary.totalEntries} />
                    <MoodStat mood="NEGATIVE" count={summary.moods.NEGATIVE} total={summary.totalEntries} />
                    <MoodStat mood="NEUTRAL" count={summary.moods.NEUTRAL} total={summary.totalEntries} />
                  </div>
                </div>

                <div>
                  <h3 className="font-bold text-lg">Key Insights</h3>
                  <div className="mt-2 space-y-2">
                    <p><strong>Your most common feeling was:</strong> <span className="font-semibold text-purple-700">{summary.topEmotion}</span></p>
                    <p><strong>Activity most linked to positive days:</strong> <span className="font-semibold text-orange-700">{summary.topActivity}</span></p>
                  </div>
                </div>
                
                <div className="bg-green-50 p-3 rounded-lg border border-green-200">
                  <p className="text-green-800"><strong>Pro-Tip:</strong> To boost your mood next week, try focusing on activities like **{summary.topActivity}**!</p>
                </div>
              </div>
            ) : (
              <p className="text-center text-gray-600 py-8">Not enough data from the past week. Keep journaling to unlock your summary!</p>
            )}
          </div>
        </div>
      )}
    </>
  );
}