'use client';

import { useState } from 'react';
import axios from 'axios';
import BreathingExercise from './BreathingExercise'; // Assumes BreathingExercise.tsx is in the same folder
import GoalTracker from './GoalTracker'; // Assumes GoalTracker.tsx is in the same folder
import WeeklySummary from './WeeklySummary';

// --- Constants ---
const ALL_ACTIVITIES = ["Exercise", "Social", "Work", "Hobby", "Rest", "Nutrition"];

// --- Type Definitions ---
type AnalysisResult = {
  sentiment: 'POSITIVE' | 'NEGATIVE' | 'NEUTRAL' | string;
  labels: string[];
  activities: string[];
};

// --- Helper Components ---
const InsightCard = ({ primaryEmotion }: { primaryEmotion: string | null }) => {
  let title = "Your Personalized Insight ‚ú®";
  let text = "We've analyzed your entry. Reflect on the tags above to better understand your feelings.";
  let emoji = "üß†";

  switch (primaryEmotion) {
    case 'Anxiety':
      title = "Feeling Anxious?";
      text = "When you feel overwhelmed, try the 4-7-8 breathing technique. Inhale for 4s, hold for 7s, and exhale for 8s. It can calm your nervous system.";
      emoji = "üå¨Ô∏è";
      break;
    case 'Fatigue':
      title = "Feeling Fatigued?";
      text = "Your energy seems low. Consider a short 10-minute walk outside. A little movement and fresh air can make a world of difference.";
      emoji = "üö∂‚Äç‚ôÄÔ∏è";
      break;
    case 'Sadness':
      title = "Feeling Sad?";
      text = "It's okay to feel this way. Try writing down three small things you are grateful for today. This practice can gently shift your perspective.";
      emoji = "‚ù§Ô∏è";
      break;
  }

  return (
    <div className="mt-6 bg-blue-50 border border-blue-200 rounded-lg p-4">
      <h3 className="font-bold text-lg text-blue-800 flex items-center">{emoji} {title}</h3>
      <p className="text-blue-700 mt-2">{text}</p>
    </div>
  );
};

const CorrelationCard = ({ analysis }: { analysis: AnalysisResult }) => {
  if (!analysis.activities || analysis.activities.length === 0) {
    return null;
  }
  
  let insightText = null;
  if (analysis.sentiment === 'POSITIVE' && analysis.activities.includes('Exercise')) {
    insightText = "Great job on exercising! It seems to have a positive impact on your mood.";
  } else if (analysis.sentiment === 'POSITIVE' && analysis.activities.includes('Social')) {
    insightText = "Connecting with others is powerful. It looks like it boosted your spirits today!";
  } else if (analysis.labels.includes('Fatigue') && analysis.activities.includes('Work')) {
    insightText = "We noticed you logged 'Work' and are feeling fatigued. Remember to take regular breaks to recharge.";
  }

  if (!insightText) return null;

  return (
    <div className="mt-4 bg-green-50 border border-green-200 rounded-lg p-4">
      <h3 className="font-bold text-lg text-green-800 flex items-center">üìà Correlation Insight</h3>
      <p className="text-green-700 mt-2">{insightText}</p>
    </div>
  );
};

const Tag = ({ label, type }: { label: string, type: 'sentiment' | 'label' | 'activity' }) => {
  const colorClasses = {
    sentiment: {
      POSITIVE: 'bg-green-100 text-green-800',
      NEGATIVE: 'bg-red-100 text-red-800',
      NEUTRAL: 'bg-gray-100 text-gray-800',
    },
    label: {
      Anxiety: 'bg-purple-100 text-purple-800',
      Fatigue: 'bg-gray-100 text-gray-800',
      Sadness: 'bg-blue-100 text-blue-800',
      Work: 'bg-yellow-100 text-yellow-800',
      Family: 'bg-pink-100 text-pink-800',
      default: 'bg-indigo-100 text-indigo-800',
    },
    activity: 'bg-orange-100 text-orange-800 border border-orange-200',
  };

  let color;
  if (type === 'sentiment') {
    color = colorClasses.sentiment[label as keyof typeof colorClasses.sentiment] || colorClasses.label.default;
  } else if (type === 'label') {
    color = colorClasses.label[label as keyof typeof colorClasses.label] || colorClasses.label.default;
  } else {
    color = colorClasses.activity;
  }

  return (
    <span className={`px-3 py-1 text-sm font-medium rounded-full ${color}`}>
      {type === 'activity' && 'üèÉ‚Äç‚ôÇÔ∏è '}{label}
    </span>
  );
};


// --- Main Page Component ---
export default function HomePage() {
  const [journalText, setJournalText] = useState('');
  const [selectedActivities, setSelectedActivities] = useState<string[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [analysis, setAnalysis] = useState<AnalysisResult | null>(null);
  const [error, setError] = useState('');
  const [showBreathingExercise, setShowBreathingExercise] = useState(false);

  const handleActivityChange = (activity: string) => {
    setSelectedActivities(prev => 
      prev.includes(activity) 
        ? prev.filter(a => a !== activity) 
        : [...prev, activity]
    );
  };

  const handleAnalyze = async () => {
    if (!journalText.trim()) {
      setError('Please write something in your journal first.');
      return;
    }
    setIsLoading(true);
    setError('');
    setAnalysis(null);

    try {
      const response = await axios.post('http://127.0.0.1:8000/analyze', {
        text: journalText,
        activities: selectedActivities,
      });

      if (response.data.error) {
        throw new Error(response.data.error);
      }
      
      setAnalysis(response.data);

      // --- NEW: Save analysis to localStorage for Weekly Summary ---
      const history = JSON.parse(localStorage.getItem('aura-history') || '[]');
      const newEntry = {
        date: new Date().toISOString(),
        sentiment: response.data.sentiment,
        labels: response.data.labels,
        activities: response.data.activities,
      };
      history.push(newEntry);
      localStorage.setItem('aura-history', JSON.stringify(history));
      // --- End of new block ---

    } catch (err) {
      console.error(err);
      setError('Sorry, we couldn\'t analyze your entry. The server might be busy. Please try again.');
    } finally {
      setIsLoading(false);
    }
  };
  
  const getPrimaryEmotion = (): string | null => {
    if (!analysis) return null;
    const emotions = ['Anxiety', 'Fatigue', 'Sadness'];
    return analysis.labels.find(label => emotions.includes(label)) || null;
  };

  return (
    <>
      {/* Conditionally render the exercise modal on top of everything */}
      {showBreathingExercise && <BreathingExercise onClose={() => setShowBreathingExercise(false)} />}
      
      <main className="flex min-h-screen flex-col items-center p-4 sm:p-8 bg-gray-50 font-sans">
        <div className="w-full max-w-2xl">
          <header className="text-center mb-8">
            <h1 className="text-4xl sm:text-5xl font-bold text-gray-800">Aura Journal</h1>
            <p className="text-gray-600 mt-2">Your private space to reflect and understand your feelings.</p>
          </header>

          {/* Journal Input Section */}
          <div className="bg-white p-6 rounded-lg shadow-md">
            <textarea
              value={journalText}
              onChange={(e) => setJournalText(e.target.value)}
              placeholder="How are you feeling today? Let it all out..."
              className="w-full h-40 p-3 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-blue-500 transition-shadow duration-200"
              disabled={isLoading}
            />

            <div className="mt-4">
              <h3 className="text-md font-semibold text-gray-700 mb-2">What did you do today? (Optional)</h3>
              <div className="flex flex-wrap gap-2">
                {ALL_ACTIVITIES.map(activity => (
                  <button
                    key={activity}
                    onClick={() => handleActivityChange(activity)}
                    className={`px-3 py-1.5 text-sm font-medium rounded-full border transition-colors ${
                      selectedActivities.includes(activity)
                        ? 'bg-blue-500 text-white border-blue-500'
                        : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-100'
                    }`}
                  >
                    {activity}
                  </button>
                ))}
              </div>
            </div>
            
            <button
              onClick={handleAnalyze}
              disabled={isLoading}
              className="w-full mt-6 bg-blue-600 text-white font-bold py-3 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors duration-200"
            >
              {isLoading ? 'Analyzing...' : 'Analyze My Feelings'}
            </button>
          </div>

          {/* Goal Tracker Section */}
          <GoalTracker />

          {/* NEW: Weekly Summary Section */}
          <WeeklySummary />

          {error && <p className="text-red-500 text-center mt-4">{error}</p>}
          
          {/* Analysis Results Section */}
          {analysis && (
            <div className="mt-8 bg-white p-6 rounded-lg shadow-md animate-fade-in">
              <h2 className="text-2xl font-bold text-gray-800 mb-4">Your Analysis</h2>
              <div className="flex flex-wrap gap-2">
                <Tag label={analysis.sentiment} type="sentiment"/>
                {analysis.labels.map(label => <Tag key={label} label={label} type="label" />)}
                {analysis.activities.map(activity => <Tag key={activity} label={activity} type="activity" />)}
              </div>
              <InsightCard primaryEmotion={getPrimaryEmotion()} />
              <CorrelationCard analysis={analysis} />
              
              {/* Guided Exercise Button */}
              {analysis.labels.includes("Anxiety") && (
                <button
                  onClick={() => setShowBreathingExercise(true)}
                  className="w-full mt-4 bg-purple-600 text-white font-bold py-2 px-4 rounded-md hover:bg-purple-700 transition-colors"
                >
                  Try a Guided Breathing Exercise
                </button>
              )}
            </div>
          )}
        </div>
      </main>
    </>
  );
}