'use client';

import { useState, useEffect } from 'react';

// Define the props the component will accept, including a function to close it
interface BreathingExerciseProps {
  onClose: () => void;
}

export default function BreathingExercise({ onClose }: BreathingExerciseProps) {
  const [instruction, setInstruction] = useState("Get ready...");
  const [cycle, setCycle] = useState(0);

  useEffect(() => {
    // A simple timer to cycle through breathing instructions
    const instructions = ["Inhale for 4s", "Hold for 7s", "Exhale for 8s"];
    const durations = [4000, 7000, 8000];

    if (cycle >= 12) { // End after 4 full cycles
      onClose();
      return;
    }

    setInstruction(instructions[cycle % 3]);
    const timer = setTimeout(() => {
      setCycle(cycle + 1);
    }, durations[cycle % 3]);

    return () => clearTimeout(timer);
  }, [cycle, onClose]);

  return (
    <div className="fixed inset-0 bg-blue-900 bg-opacity-90 flex flex-col items-center justify-center z-50 animate-fade-in">
      <div className="text-center">
        <div className="w-48 h-48 rounded-full border-4 border-white flex items-center justify-center transition-transform duration-1000">
          <p className="text-white text-3xl font-bold">{instruction}</p>
        </div>
      </div>
      <button onClick={onClose} className="absolute top-4 right-4 text-white text-2xl font-bold">&times;</button>
    </div>
  );
}